.header {
  display: flex;
  align-items: center;
  min-height: 50px;
  padding: 0.5rem 1rem;
  background-color: #131313;
  color: #e50914;
  margin-bottom: 1rem;
}

.header a {
  color: #e50914;
  text-decoration: none;
}

.header-title {
  font-size: 1.2rem;
  font-weight: bold;
}

.header-subtitle {
  font-size: 1.2rem;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
}

.carousels {
  flex: 3;
}

.carousel-single {
  position: relative;
  color: white;
}

.carousels__list-container {
  display: flex;
  overflow-x: auto;
  list-style: none;
  padding-left: 0;
  position: relative;
  scroll-behavior: smooth;
}

.carousels__list-container::-webkit-scrollbar {
  display: none;
}

.carousels__list-container > li {
  width: 190px;
  position: relative;
}

.carousels__list-container li img {
  width: 190px;
}

.carousels__list-container li .overlay ul {
  width: 100%;
  list-style: none;
  padding: 0;
}

.carousels__list-container li .overlay ul li {
  text-align: center;
}

.carousels__list-container li .overlay {
  position: absolute;
  width: 190px;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  opacity: 0;
  transition: opacity 0.2s;
  padding: 1rem;
}

.carousels__list-container > li:hover .overlay {
  opacity: 1;
}

.carousels__list-container > li:not(:last-child) {
  margin-right: 16px;
}

.carousels__list-container h3 {
  word-wrap: wrap;
}

body {
  background-color: #131313;
}

.button-container {
  display: flex;
  margin-top: 16px;
  gap: 8px;
}

.button-container button {
  border: solid 1px white;
  border-radius: 4px;
  background: none;
  padding: 8px;
  color: white;
  cursor: pointer;
}

.button-container button:hover {
  background-color: white;
  color: black;
}
